/* poppins-regular - latin */
@font-face {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/poppins-v15-latin-regular.woff2") format("woff2"); /* Super Modern Browsers */
  src: url("../fonts/poppins-v15-latin-regular.woff") format("woff"); /* Modern Browsers */
}

