          box-sizing: inherit; }

body {
  font-family: "Poppins", "Tahoma", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: calc(20.8 / 16);
  color: #1d1613;
  background-color: #ffffff;
  width: 100%;
  min-height: 100%;
  margin: 0; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

img {
  max-width: 100%; }

a {
  text-decoration: none;
  color: #1d1613;
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease; }
  a:hover, a:focus {
    color: #a87b62; }
  a:active {
    color: rgba(168, 123, 98, 0.6); }

